@import url(https://fonts.googleapis.com/css?family=Righteous);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  font-family: 'Righteous', cursive;
  border: none !important;
  margin: 5px;
}

.App {
  text-align: center;
}

.navbar {
  background-color: transparent;
}

.hero {
  padding-top: 20px;
  background-color: transparent !important;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

/* appear on page load */
.fade-appear {
  opacity: 0;
  z-index: 1
}

.fade-appear.fade-appear-active {
  opacity: 1;
  -webkit-transition: opacity 600ms linear;
  transition: opacity 600ms linear;
}

/* enter onto page */
.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-appear.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 600ms linear;
  transition: opacity 600ms linear;
}

/* disappear on element exit */
.fade-exit {
  opacity: 0;
}

.fade-exit.fade-exit-active {
  opacity: 1;
  -webkit-transition: opacity 600ms linear;
  transition: opacity 600ms linear;
}

.fade-exit-done {
  opacity: 0;
}

.card {
  background-color: rgba(255, 255, 255, 0.1);
}

.cards-container {
  padding: 30px;
  border-radius: 8px;
  /* background-color: rgba(255, 255, 255, 0.1) */
}

@-webkit-keyframes cardHover {
  from {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
  to {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    background-color: transparent;
  }
}

@keyframes cardHover {
  from {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
  to {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    background-color: transparent;
  }
}

.card:hover {
  -webkit-animation: cardHover 200ms 1 linear;
          animation: cardHover 200ms 1 linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes wobble-radium-animation-79728c0b {
  from{-webkit-transform: none;transform: none;}
  15%{-webkit-transform: translate3d(-25%, 0, 0) rotate3d(0, 0, 1, -5deg);transform: translate3d(-25%, 0, 0) rotate3d(0, 0, 1, -5deg);}
  30%{-webkit-transform: translate3d(20%, 0, 0) rotate3d(0, 0, 1, -5deg);transform: translate3d(20%, 0, 0) rotate3d(0, 0, 1, -5deg);}
  45%{-webkit-transform: translate3d(-15%, 0, 0) rotate3d(0, 0, 1, -3deg);transform: translate3d(-15%, 0, 0) rotate3d(0, 0, 1, -3deg);}
  60%{-webkit-transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 2deg);transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 2deg);}
  75%{-webkit-transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1deg);transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1deg);}
  to{-webkit-transform: none;transform: none;}
}

@keyframes wobble-radium-animation-79728c0b {
  from{-webkit-transform: none;transform: none;}
  15%{-webkit-transform: translate3d(-25%, 0, 0) rotate3d(0, 0, 1, -5deg);transform: translate3d(-25%, 0, 0) rotate3d(0, 0, 1, -5deg);}
  30%{-webkit-transform: translate3d(20%, 0, 0) rotate3d(0, 0, 1, -5deg);transform: translate3d(20%, 0, 0) rotate3d(0, 0, 1, -5deg);}
  45%{-webkit-transform: translate3d(-15%, 0, 0) rotate3d(0, 0, 1, -3deg);transform: translate3d(-15%, 0, 0) rotate3d(0, 0, 1, -3deg);}
  60%{-webkit-transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 2deg);transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 2deg);}
  75%{-webkit-transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1deg);transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1deg);}
  to{-webkit-transform: none;transform: none;}
}

.cards-container-animate {
  -webkit-animation: wobble-radium-animation-79728c0b 1s 1 linear;
          animation: wobble-radium-animation-79728c0b 1s 1 linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

